"use client";

import { Permission, Role } from '@/interface/types';
import apiClient from '@/lib/AxiosUtils';
import React, { useEffect, useState } from 'react';

const RolePermission = () => {
  const [roles, setRoles] = useState<Role[]>([]);
  const [allPermissions, setAllPermissions] = useState<Permission[]>([]);

  const getAllPermissions = async () => {
    try {
      const res = await apiClient.get(`/permissions/view`);
      console.log("Permissions:", res);
      // Make sure the structure matches your backend response
      setAllPermissions(res.data.result as Permission[]);
    } catch (error) {
      alert("Error in getting permissions");
    }
  };

  const getAllRoles = async () => {
    try {
      const res = await apiClient.get(`/permissions/view-all-roles`);
      console.log("Roles:", res);
      setRoles(res.data.result as Role[]);
    } catch (error) {
      alert("Error in getting roles");
    }
  };

  useEffect(() => {
    getAllPermissions();
    getAllRoles();
  }, []);

  return (
    <div>
      <h1>Role Permission</h1>
      <h2>Roles</h2>
      <ul>
        {roles.map(role => (
          <li key={role.roleId}>
            {role.roleTitle} - {role.roleDesc}
          </li>
        ))}
      </ul>

      <h2>All Permissions</h2>
      <ul>
        {allPermissions.map(permission => (
          <li key={permission.id}>
            {permission.permissionName} - {permission.description}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default RolePermission;
